.App{
    width : 60vw ;
    margin : auto ;
}

.Categories{
    text-align : center ;
    width : 50% ;
    margin : auto ;
}

.MainHeading{
    width : fit-content ;
    margin :  20px auto ;
    padding : 20px; 
    text-transform: capitalize ;
    letter-spacing: 2px;
    position : relative ;
    font-size : 2em ;
}
.MainHeading::after{
    position : absolute ;
    content : " "; 
    bottom : 10px ;
    left : 22% ;
    width : 50% ;
    height : 4px ;
    background-color : #c59d5f ;
}
.Categories ul{
    display : flex ;
    list-style: none ;
    justify-content: space-between;
    padding : 0px ;
}

.Categories ul li button {
    background-color : transparent ;
    color : #c59d5f;
    padding : 10px 20px ;
    outline : none ;
    border : none ;
    border-radius : 5px ;
    letter-spacing : 2px ;
    cursor : pointer ;
    text-transform: capitalize;
    transition : all .3s ;
}
.Categories ul li button:hover{
    background-color : #c59d5f;
    color : #fff ;
}

.ListContainer{
    display : flex ;
    flex-wrap : wrap ;
    justify-items: center;
    justify-content : space-between ;
}

.ListContainer .ListItem {
    display : flex ;
    padding : 20px ;
    box-sizing : border-box ;
    width : 50% ;
}

.ListContainer .ListItem .ImageContainer{
    height : 150px; 
    flex : 1 ;
    margin-right : 20px;
}
.ListContainer .ListItem .ImageContainer img{
    object-fit :  cover ;
    border : solid .25em #c59d5f;
    margin-right : 20px;
    width : 100% ;
    height : 100% ;
}

.ListContainer .ListItem .InfoContainer{
    flex : 2 ;
}
.ListContainer .ListItem .InfoContainer p{
    color : #617d98 ;
}
.ListContainer .ListItem .InfoContainer h2 {
    border-bottom:  dashed 1px #617d98;
    padding-bottom : 10px;
    letter-spacing : 2px ;
    text-transform: capitalize ;
    font-size : 1em ;
    display : flex;
    align-items : center ;
    justify-content: space-between;
}
.ListContainer .ListItem .InfoContainer h2 .Price{
    color : #c59d5f; 
}

@media screen and (max-width:1174px){
    .App{
        width : 95vw ;
    }
    .Categories {
        width : 90% ;
    }
}
@media screen and (max-width:942px){
    .ListContainer .ListItem {
        width : 95vw ;
    }
}
@media screen and (max-width:510px){
    .Categories ul li button {
        padding : 5px ;
    }
    .ListContainer .ListItem {
        display : block ;
        width : 100% ;
    }
    .ListContainer .ListItem .ImageContainer {
        margin : 0px; 
    }
}